<template>
    <g-body-layout
      :pageIndex="pageNumber"
      :pageSize="pageSize"
      :total="total"
      :filterText.sync="filterText"
      @pageIndexChange="pageIndexChange"
      @pageSizeChange="pageSizeChange"
      @suffixSearchCallback="onFilterReady"
    >
      <!-- 按钮组插槽 -->
      <template #btnGroup>
        <g-btn-group :buttonLists="btnList" @btnCallBack="btnGroupClick">
        </g-btn-group>
      </template>
      <!-- g-ndo-table插槽 -->
      <template #table>
        <g-ndo-table
          :id="id"
          :handName="'班组列表'"
          :dataList="dataList"
          :isTableLoading="loading"
          :total="total"
          @rowClick="rowClick"
        >
          <template #columns>
            <a-table-column title="班组名称" data-index="name" key="name"></a-table-column>
            <a-table-column title="组长" data-index="leader" key="leader"></a-table-column>
            <a-table-column title="成员" data-index="members" key="members">
              <template slot-scope="text">
                {{ text.join(', ') }}
              </template>
            </a-table-column>
            <a-table-column title="描述" data-index="description" key="description"></a-table-column>
          </template>
        </g-ndo-table>
      </template>
      <!-- 详细内容插槽 -->
      <template #content>
        <create-or-edit-team
          :ndoEditParams="ndoEditParams"
          :refreshTag="refreshTag"
          :saveTag="saveTag"
          :resetTag="resetTag"
          @childRefresh="childRefresh($event)"
          @rowClick="rowClick($event)"
          v-if="isShowEdit"
        ></create-or-edit-team>
        <g-empty v-else> </g-empty>
      </template>
    </g-body-layout>
  </template>
  
  <script>
  import CreateOrEditTeam from "./create-or-edit-team/create-or-edit-team.vue";
  // ... existing code ...
  import { GNdoPageBase } from "@/utils";
  
  export default {
    name: "Team",
    mixins: [GNdoPageBase],
    components: {
      CreateOrEditTeam,
    },
  
    data() {
      return {
        isTableLoading: false,
        filterText: "",
        dataList: [], // 添加 dataList
      };
    },
  
    mounted() {},
    created() {
      this.getBtnList();
    },
    methods: {
      getBtnList() {
        // ... existing code ... (和 shift.vue 中的 getBtnList 相同)
        this.btnList = [
          {
            text: this.AppConst.button.create,
            icon: this.AppConst.icon.create,
            eventName: this.AppConst.button.create,
            display: true,
          },
          {
            text: this.AppConst.button.copy,
            icon: this.AppConst.icon.copy,
            eventName: this.AppConst.button.copy,
            display: !!this.id,
          },
  
          {
            text: this.AppConst.button.delete,
            icon: this.AppConst.icon.delete,
            eventName: this.AppConst.button.delete,
            display: !!this.id,
          },
          {
            text: this.AppConst.button.refresh,
            icon: this.AppConst.icon.refresh,
            eventName: this.AppConst.button.refresh,
            right: true,
            display: this.isShowEdit,
          },
          {
            text: this.AppConst.button.save,
            icon: this.AppConst.icon.save,
            eventName: this.AppConst.button.save,
            right: true,
            type: "primary",
            display: this.isShowEdit,
          },
        ];
      },
      copy() {
        // ... existing code ...
      },
      delete() {
        // ... existing code ...
      },
      fetchData(request) {
        this.loading = true;
        setTimeout(() => {
          request.finishedCallback();
          // 填充dataList
          let dataList = [];
          for (let i = 0; i < 25; i++) {
            dataList[i] = {
              id: i + 1,
              name: "班组" + (i * this.pageNumber + 1),
              leader: "组长" + (i * this.pageNumber + 1),
              members: ["成员1", "成员2", "成员3"],
              description: "班组描述" + (i * this.pageNumber + 1),
            };
          }
  
          // filter 过滤
          if (this.filterText) {
            dataList = dataList.filter((item) => {
              return item.name.indexOf(this.filterText) > -1;
            });
          }
  
          request.successCallback({
            items: dataList,
            totalCount: dataList.length * 2,
          });
        }, 1000);
      },
    },
  };
  </script>
  
  <style lang="scss" scoped></style>